.feedback-form {
  max-width: 480px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 4px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  padding: 8px;
}

textarea {
  resize: none;
}

.bkg-dark {
  color: #fff;
  background-color: #000;
}

.icon-day {
  width: 60px;
  height: 24px;
  background-image: url("icon-day.496fa1e1.svg");
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  border-radius: 194px;
  outline: none;
  padding: 0;
}

.icon-night {
  width: 60px;
  height: 24px;
  background-image: url("icon-night.f2705c95.svg");
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  border-radius: 194px;
  outline: none;
  padding: 0;
}

